"use client"

import { useState } from 'react'
import { Card, CardContent } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { CookieOption } from '@/app/page'

const cookieOptions: CookieOption[] = [
  { id: 1, name: "Kit Flores", description: "Decoraci√≥n con flores comestibles", color: "bg-gradient-to-br from-pink-200 to-rose-300" },
  { id: 2, name: "Kit Corazones", description: "Formas rom√°nticas y dulces", color: "bg-gradient-to-br from-red-200 to-pink-300" },
  { id: 3, name: "Kit Estrellas", description: "Brillos y formas celestiales", color: "bg-gradient-to-br from-yellow-200 to-amber-300" },
  { id: 4, name: "Kit Mariposas", description: "Delicadas decoraciones aladas", color: "bg-gradient-to-br from-purple-200 to-violet-300" },
  { id: 5, name: "Kit Navide√±o", description: "Tem√°tica festiva y alegre", color: "bg-gradient-to-br from-green-200 to-emerald-300" },
  { id: 6, name: "Kit Unicornio", description: "M√°gico y colorido", color: "bg-gradient-to-br from-indigo-200 to-purple-300" },
  { id: 7, name: "Kit Tropical", description: "Sabores y colores ex√≥ticos", color: "bg-gradient-to-br from-orange-200 to-yellow-300" },
  { id: 8, name: "Kit Vintage", description: "Estilo cl√°sico y elegante", color: "bg-gradient-to-br from-amber-200 to-orange-300" },
  { id: 9, name: "Kit Arco√≠ris", description: "Todos los colores del arco√≠ris", color: "bg-gradient-to-br from-cyan-200 to-blue-300" },
  { id: 10, name: "Kit Chocolate", description: "Rico sabor a cacao", color: "bg-gradient-to-br from-amber-300 to-brown-400" }
]

interface ImageSelectorProps {
  onConfirm: (selected: CookieOption[]) => void
}

export default function ImageSelector({ onConfirm }: ImageSelectorProps) {
  const [selectedIds, setSelectedIds] = useState<number[]>([])
  const [error, setError] = useState<string>('')

  const handleOptionClick = (optionId: number) => {
    setError('')
    
    if (selectedIds.includes(optionId)) {
      // Deseleccionar
      setSelectedIds(selectedIds.filter(id => id !== optionId))
    } else {
      // Seleccionar
      if (selectedIds.length >= 3) {
        setError('Solo puedes seleccionar m√°ximo 3 opciones')
        return
      }
      setSelectedIds([...selectedIds, optionId])
    }
  }

  const handleConfirm = () => {
    if (selectedIds.length < 2) {
      setError('Debes seleccionar al menos 2 opciones')
      return
    }
    
    if (selectedIds.length > 3) {
      setError('Solo puedes seleccionar m√°ximo 3 opciones')
      return
    }

    const selectedOptions = cookieOptions.filter(option => selectedIds.includes(option.id))
    onConfirm(selectedOptions)
  }

  return (
    <div className="max-w-6xl mx-auto">
      <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-5 gap-4 mb-8">
        {cookieOptions.map((option) => (
          <Card
            key={option.id}
            className={`cursor-pointer transition-all duration-300 transform hover:scale-105 hover:shadow-lg ${
              selectedIds.includes(option.id)
                ? 'ring-4 ring-pink-400 shadow-xl scale-105'
                : 'hover:shadow-md'
            }`}
            onClick={() => handleOptionClick(option.id)}
          >
            <CardContent className="p-4">
              <div className={`${option.color} rounded-xl h-24 mb-3 flex items-center justify-center shadow-inner`}>
                <div className="text-3xl">üç™</div>
              </div>
              <h3 className="font-semibold text-sm text-gray-800 mb-1">
                {option.name}
              </h3>
              <p className="text-xs text-gray-600">
                {option.description}
              </p>
              {selectedIds.includes(option.id) && (
                <div className="mt-2 text-center">
                  <span className="inline-block bg-pink-400 text-white text-xs px-2 py-1 rounded-full">
                    ‚úì Seleccionado
                  </span>
                </div>
              )}
            </CardContent>
          </Card>
        ))}
      </div>

      <div className="text-center">
        <div className="mb-4">
          <p className="text-sm text-gray-600 mb-2">
            Seleccionados: {selectedIds.length}/3
          </p>
          {error && (
            <p className="text-red-500 text-sm font-medium bg-red-50 py-2 px-4 rounded-lg inline-block">
              {error}
            </p>
          )}
        </div>
        
        <Button
          onClick={handleConfirm}
          disabled={selectedIds.length < 2}
          className="bg-gradient-to-r from-pink-400 to-purple-400 hover:from-pink-500 hover:to-purple-500 text-white font-semibold py-3 px-8 rounded-full transition-all duration-300 transform hover:scale-105 shadow-lg disabled:opacity-50 disabled:cursor-not-allowed disabled:transform-none"
        >
          Continuar a la Ruleta
        </Button>
      </div>
    </div>
  )
}
