"use client"

import { useState } from 'react'
import ImageSelector from '@/components/ImageSelector'
import Roulette from '@/components/Roulette'

export type CookieOption = {
  id: number
  name: string
  description: string
  color: string
}

type Phase = 'select' | 'roulette' | 'finished'

export default function Home() {
  const [phase, setPhase] = useState<Phase>('select')
  const [selectedOptions, setSelectedOptions] = useState<CookieOption[]>([])

  const handleSelection = (selected: CookieOption[]) => {
    if (selected.length >= 2 && selected.length <= 3) {
      setSelectedOptions(selected)
      setPhase('roulette')
    }
  }

  const handleFinish = () => {
    setPhase('finished')
  }

  const handleRestart = () => {
    setSelectedOptions([])
    setPhase('select')
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-pink-50 via-purple-50 to-indigo-50">
      <div className="container mx-auto px-4 py-8">
        {phase === 'select' && (
          <div className="text-center mb-8">
            <h1 className="text-4xl font-bold text-gray-800 mb-4">
              üç™ Dulce Decoraci√≥n
            </h1>
            <p className="text-lg text-gray-600 mb-8">
              Elige entre 2 y 3 kits de decoraci√≥n para galletas
            </p>
            <ImageSelector onConfirm={handleSelection} />
          </div>
        )}

        {phase === 'roulette' && (
          <div className="text-center">
            <h1 className="text-4xl font-bold text-gray-800 mb-8">
              üéØ Ruleta de la Suerte
            </h1>
            <Roulette options={selectedOptions} onFinish={handleFinish} />
          </div>
        )}

        {phase === 'finished' && (
          <div className="text-center min-h-[60vh] flex flex-col justify-center items-center">
            <div className="bg-white/80 backdrop-blur-sm rounded-3xl p-12 shadow-xl border border-pink-200">
              <div className="text-6xl mb-6">üç™‚ú®</div>
              <h1 className="text-5xl font-bold text-gray-800 mb-6">
                ¬°Perfecto!
              </h1>
              <p className="text-2xl text-gray-700 mb-8 font-medium">
                Disfrutalas, hasta pronto
              </p>
              <button
                onClick={handleRestart}
                className="bg-gradient-to-r from-pink-400 to-purple-400 hover:from-pink-500 hover:to-purple-500 text-white font-semibold py-4 px-8 rounded-full transition-all duration-300 transform hover:scale-105 shadow-lg"
              >
                Comenzar de nuevo
              </button>
            </div>
          </div>
        )}
      </div>
    </div>
  )
}
