"use client"

import { useState, useEffect } from 'react'
import { Card, CardContent } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { CookieOption } from '@/app/page'

interface RouletteProps {
  options: CookieOption[]
  onFinish: () => void
}

export default function Roulette({ options, onFinish }: RouletteProps) {
  const [remainingOptions, setRemainingOptions] = useState<CookieOption[]>(options)
  const [currentSelection, setCurrentSelection] = useState<CookieOption | null>(null)
  const [isSpinning, setIsSpinning] = useState(false)
  const [eliminatedOptions, setEliminatedOptions] = useState<CookieOption[]>([])
  const [showResult, setShowResult] = useState(false)

  const getRandomIndex = (items: CookieOption[]): number => {
    return Math.floor(Math.random() * items.length)
  }

  const handleSpin = () => {
    if (remainingOptions.length <= 1) return

    setIsSpinning(true)
    setShowResult(false)
    setCurrentSelection(null)

    // Simular el giro de la ruleta
    setTimeout(() => {
      const randomIndex = getRandomIndex(remainingOptions)
      const selectedOption = remainingOptions[randomIndex]
      
      setCurrentSelection(selectedOption)
      setShowResult(true)
      
      // Despu√©s de mostrar el resultado, eliminar la opci√≥n
      setTimeout(() => {
        const newRemaining = remainingOptions.filter((_, index) => index !== randomIndex)
        const newEliminated = [...eliminatedOptions, selectedOption]
        
        setRemainingOptions(newRemaining)
        setEliminatedOptions(newEliminated)
        setIsSpinning(false)
        setShowResult(false)
        
        // Si solo queda una opci√≥n, terminar el juego
        if (newRemaining.length === 1) {
          setTimeout(() => {
            onFinish()
          }, 1000)
        }
      }, 2000)
    }, 3000)
  }

  return (
    <div className="max-w-4xl mx-auto">
      {/* √Årea de la ruleta */}
      <div className="mb-8">
        <div className="relative bg-white/80 backdrop-blur-sm rounded-3xl p-8 shadow-xl border border-pink-200">
          <div className="text-center mb-6">
            <h2 className="text-2xl font-bold text-gray-800 mb-2">
              Opciones Restantes: {remainingOptions.length}
            </h2>
            {remainingOptions.length > 1 && (
              <p className="text-gray-600">
                Presiona "Girar" para eliminar una opci√≥n al azar
              </p>
            )}
          </div>

          {/* C√≠rculo de la ruleta */}
          <div className="relative mx-auto mb-8" style={{ width: '300px', height: '300px' }}>
            <div 
              className={`w-full h-full rounded-full border-8 border-pink-300 bg-gradient-to-br from-pink-100 to-purple-100 flex items-center justify-center transition-all duration-300 ${
                isSpinning ? 'animate-spin' : ''
              }`}
            >
              {isSpinning ? (
                <div className="text-4xl">üéØ</div>
              ) : showResult && currentSelection ? (
                <div className="text-center">
                  <div className="text-3xl mb-2">üç™</div>
                  <div className="text-lg font-bold text-gray-800">
                    {currentSelection.name}
                  </div>
                  <div className="text-red-500 font-semibold mt-2">
                    ¬°Eliminado!
                  </div>
                </div>
              ) : (
                <div className="text-center">
                  <div className="text-4xl mb-2">üé≤</div>
                  <div className="text-gray-600">
                    Lista para girar
                  </div>
                </div>
              )}
            </div>
          </div>

          {/* Bot√≥n de girar */}
          {remainingOptions.length > 1 && (
            <div className="text-center">
              <Button
                onClick={handleSpin}
                disabled={isSpinning}
                className="bg-gradient-to-r from-pink-400 to-purple-400 hover:from-pink-500 hover:to-purple-500 text-white font-semibold py-4 px-8 rounded-full transition-all duration-300 transform hover:scale-105 shadow-lg disabled:opacity-50 disabled:cursor-not-allowed disabled:transform-none"
              >
                {isSpinning ? 'Girando...' : 'Girar Ruleta'}
              </Button>
            </div>
          )}

          {remainingOptions.length === 1 && (
            <div className="text-center">
              <div className="text-6xl mb-4">üèÜ</div>
              <h3 className="text-3xl font-bold text-gray-800 mb-2">
                ¬°Ganador!
              </h3>
              <div className="bg-gradient-to-r from-yellow-200 to-orange-200 rounded-2xl p-6 inline-block">
                <div className={`${remainingOptions[0].color} rounded-xl p-4 mb-2`}>
                  <div className="text-4xl">üç™</div>
                </div>
                <div className="text-xl font-bold text-gray-800">
                  {remainingOptions[0].name}
                </div>
                <div className="text-gray-600">
                  {remainingOptions[0].description}
                </div>
              </div>
            </div>
          )}
        </div>
      </div>

      {/* Opciones restantes */}
      {remainingOptions.length > 1 && (
        <div className="mb-6">
          <h3 className="text-xl font-semibold text-gray-800 mb-4 text-center">
            Opciones en Juego
          </h3>
          <div className="grid grid-cols-2 md:grid-cols-3 gap-4">
            {remainingOptions.map((option) => (
              <Card key={option.id} className="transition-all duration-300">
                <CardContent className="p-4 text-center">
                  <div className={`${option.color} rounded-xl h-16 mb-2 flex items-center justify-center`}>
                    <div className="text-2xl">üç™</div>
                  </div>
                  <h4 className="font-semibold text-sm text-gray-800">
                    {option.name}
                  </h4>
                </CardContent>
              </Card>
            ))}
          </div>
        </div>
      )}

      {/* Opciones eliminadas */}
      {eliminatedOptions.length > 0 && (
        <div>
          <h3 className="text-lg font-semibold text-gray-600 mb-4 text-center">
            Opciones Eliminadas
          </h3>
          <div className="grid grid-cols-2 md:grid-cols-3 gap-3">
            {eliminatedOptions.map((option) => (
              <Card key={option.id} className="opacity-60 transition-all duration-300">
                <CardContent className="p-3 text-center">
                  <div className={`${option.color} rounded-xl h-12 mb-2 flex items-center justify-center grayscale`}>
                    <div className="text-lg">üç™</div>
                  </div>
                  <h4 className="font-semibold text-xs text-gray-500">
                    {option.name}
                  </h4>
                  <div className="text-red-400 text-xs mt-1">
                    ‚úó Eliminado
                  </div>
                </CardContent>
              </Card>
            ))}
          </div>
        </div>
      )}
    </div>
  )
}
